/* Global page styling */
body {
  font-family: Arial, sans-serif;
  font-size: 18px; /* Bigger font for whole page */
  margin: 0;
  padding: 20px; /* Space from edges of browser */
  background-color: #e8efff;
  color: #1f2937;
}

/* Headings */
h1, h2, h3 {
  margin-top: 0;
  font-weight: bold;
}

/* Form elements */
select, button {
  font-size: 18px;
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

/* Button style */
button {
  background: #3b82f6;
  color: white;
  cursor: pointer;
}

button:hover {
  background: #2563eb;
}

/* Slider styles */
input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  height: 14px; /* Track height */
  background: #ddd;
  border-radius: 7px;
  margin: 10px 0;
}

/* WebKit track */
input[type="range"]::-webkit-slider-runnable-track {
  height: 14px;
  background: #3b82f6;
  border-radius: 7px;
}

/* WebKit thumb */
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 26px;
  width: 26px;
  background: white;
  border: 2px solid #3b82f6;
  border-radius: 50%;
  cursor: pointer;
  margin-top: -6px; /* Center thumb vertically */
}

/* Firefox track */
input[type="range"]::-moz-range-track {
  height: 14px;
  background: #3b82f6;
  border-radius: 7px;
}

/* Firefox thumb */
input[type="range"]::-moz-range-thumb {
  height: 26px;
  width: 26px;
  background: white;
  border: 2px solid #3b82f6;
  border-radius: 50%;
  cursor: pointer;
}

